.controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    padding-top: 0;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    min-height: 50px;
}

.buttons-container {
    display: flex;
    justify-content: space-between;
    gap: 8px;
    align-items: center;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
}

.control-buttons-container {
    display: flex;
    flex-direction: row;
    gap: 8px;
}

.control-button {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    background-color: #f0f0f0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
    padding: 0;
    border: 1.5px solid #b1b1b1;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
}

.control-button:hover:not(:disabled) {
    background-color: #e0e0e0;
    transform: scale(1.05);
    border-color: #bdbdbd;
}

.control-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.control-button.hint-button {
    background-color: #e3f2fd;
    border-color: #90caff;
    font-size: 1.2em;
    font-weight: 800;
    color: #90caff;
}

.control-button.hint-button:hover:not(:disabled) {
    background-color: #bbdefb;
    border-color: #64b5f6;
}

.control-button.hint-button svg {
    color: #1976d2;
}

.control-button.error-button {
    background-color: #ffeaea;
    color: #c62828;
    border: 1.5px solid #ffbdbd;
    font-weight: 600;
}

.control-button.error-button:hover:not(:disabled) {
    background-color: #ffd6d6;
    border-color: #ff8a8a;
}

.control-button.error-button svg {
    color: #c62828;
}

.control-button svg {
    width: 24px;
    height: 24px;
}

/* Botão de sucesso (continuar) */
.control-button.success-button {
    background-color: #e6fbe6;
    color: #176c1a;
    border: 1.5px solid #b6e2b6;
    font-weight: 600;
}

.control-button.success-button:hover:not(:disabled) {
    background-color: #c8f7c8;
    border-color: #7ed87e;
}

.control-button.success-button svg {
    color: #176c1a;
}

.control-button.unified-feedback-button {
    min-width: 300px;
    font-size: 1.08em;
    padding: 0 18px;
    border-radius: 12px;
    height: 44px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.control-button.unified-feedback-button .feedback-icon {
    width: 22px;
    height: 22px;
    margin-left: 10px;
}

.control-button.unified-feedback-button svg,
.control-button.unified-feedback-button .refresh-icon {
    display: inline-block;
    vertical-align: middle;
    align-self: center;
}

.control-button.unified-feedback-button.error-button {
    background-color: #ffeaea;
    color: #c62828;
    border: 1.5px solid #ffbdbd;
}

.control-button.unified-feedback-button.error-button:hover:not(:disabled) {
    background-color: #ffd6d6;
    border-color: #ff8a8a;
}

.control-button.unified-feedback-button.success-button {
    background-color: #e6fbe6;
    color: #176c1a;
    border: 1.5px solid #b6e2b6;
}

.control-button.unified-feedback-button.success-button:hover:not(:disabled) {
    background-color: #c8f7c8;
    border-color: #7ed87e;
}

.control-button.unified-feedback-button.success-button svg {
    color: #176c1a;
}
  
  
.turn-indicator-container {
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}