.dropdowns-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 400px;
    margin: 0 auto 12px auto;
    gap: 10px;
    margin-top: 1rem;
  }
  
  .dropdown-wrapper {
    position: relative;
    width: 100%;
  }
  
  .dropdown-tema, .dropdown-nivel {
    width: 100%;
    max-width: 100vw;
    padding: 8px 48px 8px 14px;
    border-radius: 4px;
    border: 1px solid hsl(0, 0%, 73%, 0.7);
    background: #ffffff;
    font-size: 14px;
    font-family: 'Segoe UI', 'Roboto', Arial, sans-serif;
    color: #333;
    box-shadow: 0 1px 4px rgba(0,0,0,0.03);
    outline: none;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: none;
    text-align: left;
  }
  
  .dropdown-tema option, .dropdown-nivel option {
    padding: 8px;
    text-align: left;
  }
  
  .dropdown-chevron {
    position: absolute;
    right: 18px;
    top: 50%;
    width: 24px;
    height: 24px;
    pointer-events: none;
    transform: translateY(-50%);
    background-image: url('../../icons/chevron-down.svg');
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: center;
    display: block;
    right: 10px;
    width: 22px;
    height: 22px;
    background-size: 22px 22px;
    
  }
  
  .dropdown-tema:hover, .dropdown-nivel:hover {
    cursor: pointer;
  }
  