.App {
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
  padding: 0 16px;
  margin-bottom: 2rem;
}

.chessboard-info {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 400px;
}

.exercise {
  text-align: left;
  font-size: 0.85em;
  padding: 3px 0;
  color: #666;
  text-align: left;
  margin: 2px 0;
  opacity: 0.8;
  margin-left: 15px;
}

.level {
  text-align: "right";
  margin-right: 15px;
  color: "#000";
  font-size: 1.15em;
}
