.pwa-banner {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 16px;
  box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  transform: translateY(100%);
  animation: slideUp 0.3s ease-out forwards;
}

.pwa-banner-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}

.pwa-banner-text h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  font-weight: 600;
}

.pwa-banner-text p {
  margin: 0;
  font-size: 14px;
  opacity: 0.9;
  line-height: 1.4;
}

.pwa-banner-actions {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.pwa-banner-button {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.pwa-banner-button-primary {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.pwa-banner-button-primary:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
}

.pwa-banner-button-secondary {
  background: transparent;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.pwa-banner-button-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Botão discreto de reativação */
.pwa-reactivate-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}

.pwa-reactivate-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: pulse 2s infinite;
}

.pwa-reactivate-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.pwa-reactivate-btn:active {
  transform: scale(0.95);
}

/* Variantes específicas */
.pwa-update-banner {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

.pwa-offline-banner {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

/* Animações */
@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
  }
  100% {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  }
}

/* Responsividade */
@media (max-width: 768px) {
  .pwa-banner-content {
    flex-direction: column;
    text-align: center;
    gap: 12px;
  }

  .pwa-banner-text h3 {
    font-size: 15px;
  }

  .pwa-banner-text p {
    font-size: 13px;
  }

  .pwa-banner-actions {
    width: 100%;
    justify-content: center;
  }

  .pwa-banner-button {
    flex: 1;
    max-width: 120px;
  }

  .pwa-reactivate-button {
    bottom: 15px;
    right: 15px;
  }

  .pwa-reactivate-btn {
    width: 45px;
    height: 45px;
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .pwa-banner {
    padding: 12px;
  }

  .pwa-banner-text h3 {
    font-size: 14px;
  }

  .pwa-banner-text p {
    font-size: 12px;
  }

  .pwa-banner-button {
    padding: 6px 12px;
    font-size: 13px;
  }

  .pwa-reactivate-button {
    bottom: 10px;
    right: 10px;
  }

  .pwa-reactivate-btn {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }
}

/* Ajuste para quando o banner está visível */
body.pwa-banner-visible {
  padding-bottom: 80px;
}

@media (max-width: 768px) {
  body.pwa-banner-visible {
    padding-bottom: 120px;
  }
} 