.chessboard-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 8px;
    overflow: hidden;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
  }
  
  .chessboard-container > div {
    border: 1px solid #ccc;
    border-radius: 4px;
    overflow: hidden;
    width: 100% !important;
  }
 